const initSticky = () => {
  const stickyFilter = document.querySelector('.filters');
  const minWidthDesktop = window.matchMedia('(min-width: 768px)');
  const maxWidthMobile = window.matchMedia('(max-width: 767px)');

  if (!stickyFilter) {
    return;
  }

  if (stickyFilter) {
    const heightHeader = document.querySelector('.header').offsetHeight;
    let indentWithoutCatalog = heightHeader;
    let indentWithCatalog;

    const changesStylesOnResize = () => {
      if (minWidthDesktop) {
        indentWithCatalog = 14;
        changesСlass();
      } else if (maxWidthMobile) {
        indentWithCatalog = 0;
        changesСlass();
      }
    };

    const changesСlass = () => {
      if (stickyFilter.getBoundingClientRect().top === indentWithCatalog) {
        stickyFilter.classList.add('filters--active');
      } else if (stickyFilter.getBoundingClientRect().top !== indentWithoutCatalog) {
        stickyFilter.classList.remove('filters--active');
      }
    };

    changesStylesOnResize();

    window.addEventListener('resize', () => {
      changesStylesOnResize();
    }, false);

    window.addEventListener('scroll', () => {
      changesСlass();
    });
  }
};

export {initSticky};
