const initCatalogFilter = () => {
  const body = document.querySelector('.page-body');
  const filters = body.querySelectorAll('.filters-item__wrapper');

  if (filters.length <= 0) {
    return;
  }

  if (filters) {
    const formFilters = body.querySelector('.form-filters__list');
    const overlay = body.querySelector('.overlay');
    const brandsList = formFilters.querySelector('.brands-list');
    const brandsSublist = formFilters.querySelector('.brands-sublist');
    const brandsLink = brandsList.querySelectorAll('.brands-item__link');
    const brandsItem = brandsSublist.querySelectorAll('.brands-item');

    const initCardSublist = () => {
      brandsLink.forEach((i) => {
        i.addEventListener('click', (evt) => {
          evt.preventDefault();
          brandsItem.forEach((e) => {
            if (i.dataset.parent === e.dataset.parent) {
              brandsSublist.classList.add('brands-sublist--active');
              e.classList.add('brands-item--active');
            }
          });
        });
      });
    };

    const removesCardSublist = () => {
      brandsItem.forEach((e) => {
        if (e.classList.contains('brands-item--active')) {
          e.classList.remove('brands-item--active');
        }
      });
    };

    const initLinkSublist = () => {
      brandsItem.forEach((e) => {
        const itemLink = e.querySelector('.brands-item__link');

        itemLink.addEventListener('click', (evt) => {
          evt.preventDefault();
          if (e.classList.contains('brands-item--active')) {
            e.classList.remove('brands-item--active');
            brandsSublist.classList.remove('brands-sublist--active');
          }
        });
      });
    };

    filters.forEach((item) => {
      item.addEventListener('click', (evt) => {
        evt.preventDefault();
        const parent = item.parentNode;
        const filtersList = parent.querySelector('.filters-item__list');
        let leftIndentItem = parent.getBoundingClientRect().left;
        let leftIndentList = formFilters.getBoundingClientRect().left;
        let leftIndentFilter = leftIndentItem - leftIndentList;

        const keepsTrackResizing = () => {
          filtersList.style.left = leftIndentFilter + 'px';
          brandsList.style.height = brandsList.scrollHeight + 1 + 'px';
          brandsList.style.maxHeight = window.innerHeight - formFilters.getBoundingClientRect().bottom + 'px';
        };

        if (parent.classList.contains('filters__item--active')) {
          parent.classList.remove('filters__item--active');
          overlay.classList.remove('overlay--active');
          body.classList.remove('page-body--filter');
          brandsSublist.classList.remove('brands-sublist--active');
          removesCardSublist();
        } else {
          document.querySelectorAll('.form-filters__item').forEach((element) => {
            element.classList.remove('filters__item--active');
            overlay.classList.remove('overlay--active');
            body.classList.remove('page-body--filter');
          });
          parent.classList.add('filters__item--active');
          overlay.classList.add('overlay--active');
          body.classList.add('page-body--filter');
          keepsTrackResizing();

          window.addEventListener('resize', () => {
            keepsTrackResizing();
          }, false);
        }

        initCardSublist();
        initLinkSublist();

        overlay.addEventListener('click', (e) => {
          if (parent.classList.contains('filters__item--active')) {
            e.preventDefault();
            parent.classList.remove('filters__item--active');
            overlay.classList.remove('overlay--active');
            body.classList.remove('page-body--filter');
            brandsSublist.classList.remove('brands-sublist--active');
            removesCardSublist();
          }
        });
      });
    });
  }
};

export {initCatalogFilter};
