
const sliderBase = document.querySelector('.product-slider');
const pageWrapper = document.querySelector('.wrapper');
const SLIDER_TRANSLATION = {
  enabled: true,
  firstSlideMessage: 'Первое фото',
  lastSlideMessage: 'Последнее фото',
  nextSlideMessage: 'Следующее фото',
  prevSlideMessage: 'Предыдуще фото',
  paginationBulletMessage: 'Перейти к слайду под номером {{index}}',
  slideLabelMessage: 'Фото {{index}} из {{slidesLength}}',
};

const initProductSlider = () => {
  if (sliderBase) {
    const sliderMainBase = sliderBase.querySelector('.product-slider__main');
    const sliderThumbsBase = sliderBase.querySelector('.product-slider__thumbs');
    const sliderThumbsSlides = sliderThumbsBase.querySelectorAll('.product-slider__thumbs-item');
    const modalOverlay = sliderBase.querySelector('.product-slider__modal-overlay');
    const sliderModalBase = sliderBase.querySelector('.product-slider__modal');
    const sliderModalButtonClose = sliderBase.querySelector('.product-slider__modal-button-close');

    const swiperThumbs = new window.Swiper(sliderThumbsBase, {
      spaceBetween: 10,
      slidesPerView: 'auto',
      freeMode: true,
      watchSlidesProgress: true,

      a11y: SLIDER_TRANSLATION,
    });

    const swiperMain = new window.Swiper(sliderMainBase, {
      spaceBetween: 10,
      navigation: {
        nextEl: '.product-slider__button--next',
        prevEl: '.product-slider__button--prev',
        disabledClass: 'product-slider__button--disabled',
      },
      thumbs: {
        swiper: swiperThumbs,
      },
      a11y: SLIDER_TRANSLATION,
    });

    swiperMain.on('activeIndexChange', () => {
      const sliderThumbsActiveClass = 'product-slider__thumbs-item--active';

      sliderThumbsSlides.forEach((slide) => {
        slide.classList.remove(sliderThumbsActiveClass);
      });
      sliderThumbsSlides[swiperMain.activeIndex].classList.add(sliderThumbsActiveClass);
    });

    const swiperModal = new window.Swiper(sliderModalBase, {
      spaceBetween: 10,
      navigation: {
        nextEl: '.product-slider__button-modal--next',
        prevEl: '.product-slider__button-modal--prev',
        disabledClass: 'product-slider__button--disabled',
      },
      keyboard: {
        enabled: true,
        onlyInViewport: false,
      },
      a11y: SLIDER_TRANSLATION,
    });

    const openSliderModal = () => {
      modalOverlay.classList.add('product-slider__modal-overlay--active');
      swiperModal.slideTo(swiperMain.activeIndex, 1);
      pageWrapper.classList.add('wrapper--no-scrollbar');
    };

    const closeSliderModal = () => {
      modalOverlay.classList.remove('product-slider__modal-overlay--active');
      swiperMain.slideTo(swiperModal.activeIndex, 1);
      pageWrapper.classList.remove('wrapper--no-scrollbar');
    };

    sliderMainBase.addEventListener('click', (evt) => {
      if (evt.target.tagName === 'IMG') {
        openSliderModal();

        modalOverlay.addEventListener('click', (e) => {
          if (e.target === modalOverlay) {
            closeSliderModal();
          }
        });

        sliderModalButtonClose.addEventListener('click', () => {
          closeSliderModal();
        });

        window.addEventListener('keydown', (e) => {
          if (e.key === 'Escape') {
            closeSliderModal();
          }
        });
      }
    });
  }


};

export {initProductSlider};
