/* globals Swiper */
function initPromoSlider() {
  const sliderPromo = document.querySelector('.promo__slider');

  if (!sliderPromo) {
    return;
  }
  // eslint-disable-next-line
  const slider = new Swiper(sliderPromo, {
    a11y: {
      enabled: true,
      firstSlideMessage: 'Первый слайд',
      lastSlideMessage: 'Последний слайд',
      nextSlideMessage: 'Следующий слайд',
      prevSlideMessage: 'Предыдущий слайд',
      paginationBulletMessage: 'Перейти к слайду {{index}}',
      slideLabelMessage: 'Слайд {{index}} из {{slidesLength}}',
    },
    wrapperClass: 'promo__slides',
    slideClass: 'promo__slide',
    slideActiveClass: 'promo__slide--active',
    slidePrevClass: 'promo__slide--prev',
    slideNextClass: 'promo__slide--next',
    loop: true,
    pagination: {
      el: '.promo__pagination',
      clickable: true,
      type: 'bullets',
      bulletElement: 'button',
      bulletClass: 'pagination__button pagination__button--rectangle',
      bulletActiveClass: 'pagination__button--active',
      renderBullet(index, className) {
        return `<button class="${className}" type="button" data-slide="${++index}"></button>`;
      },
    },
    keyboard: {
      enabled: true,
    },
  });

  // eslint-disable-next-line consistent-return
  return slider;
}

export {initPromoSlider};
