import {iosVhFix} from './utils/ios-vh-fix';
import {initModals} from './modules/modals/init-modals';
import {initCustomSelect} from './modules/form/init-custom-select';
import {initFormValidate} from './modules/form/init-form-validate';
import {initPromoSlider} from './modules/slider/promo-slider';
// import {initCatalogSidebar} from './modules/sidebar';
import {initCatalogFilter} from './modules/catalog-filter';
import {initGeographySlider} from './modules/slider/geography-slider';
import {initProductSlider} from './modules/slider/product-slider';
import {initTabs} from './modules/tabs/init-tabs';
import {initRange} from './modules/range';
import {initActiveIcons} from './modules/active-icons';
import {initNavToggle} from './modules/navigation/main-nav';
import {initCalcRent} from './modules/calc-rent';
import {initSticky} from './modules/sticky';
import {initMobileMenu} from './modules/navigation/mobile-menu';
import {initFilterBar} from './modules/filter-bar';
import {initScrollSequence} from './modules/scroll-sequence';
import {initAccordions} from './modules/init-accordion';

// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  iosVhFix();


  // Modules
  // ---------------------------------
  initPromoSlider();
  initGeographySlider();
  initTabs();
  initNavToggle();
  initMobileMenu();
  initProductSlider();
  initAccordions();

  // все скрипты должны быть в обработчике 'DOMContentLoaded', но не все в 'load'
  // в load следует добавить скрипты, не участвующие в работе первого экрана
  window.addEventListener('load', () => {
    initModals();
    initCustomSelect();
    initFormValidate();
    // initCatalogSidebar();
    initCatalogFilter();
    initRange();
    initActiveIcons();
    initCalcRent();
    initSticky();
    initFilterBar();
    initScrollSequence();
  });
});

