const initFilterBar = () => {
  const filters = document.querySelector('.filters');

  if (!filters) {
    return;
  }

  if (filters) {
    const body = document.querySelector('.page-body');
    const overlay = document.querySelector('.overlay');
    const filtersList = filters.querySelector('.filters__wrapper');
    const buttonFilters = filters.querySelector('.filters__toggle');
    const filtersItem = filters.querySelectorAll('.filters-item');

    if (buttonFilters) {
      buttonFilters.addEventListener('click', () => {
        if (buttonFilters.classList.contains('filters__toggle--active')) {
          buttonFilters.classList.remove('filters__toggle--active');
          filtersList.classList.remove('filters__wrapper--active');
          overlay.classList.remove('overlay--active');
          body.classList.remove('page-body--filter');
        } else if (!buttonFilters.classList.contains('filters__toggle--active')) {
          buttonFilters.classList.add('filters__toggle--active');
          filtersList.classList.add('filters__wrapper--active');
          filtersItem.forEach((item) => {
            if (item.classList.contains('filters__item--active')) {
              overlay.classList.add('overlay--active');
              body.classList.add('page-body--filter');
            }
          });
        }
      });
    }
  }
};


export {initFilterBar};
